import { MoveListShared } from 'components/move-list-shared/config/types';
import { setCSSVariable } from 'utils/dom/set-css-variable';

/**
 * Add a custom color to a node element.
 */
export function addCustomColor(el: HTMLElement, color?: string): void {
  Object.values(MoveListShared.CssVariables).forEach(setVariable);

  /**
   * Set the css variable.
   */
  function setVariable(variable: string) {
    setCSSVariable({
      el,
      value: color,
      variable,
    });
  }
}
