import { Board } from 'chessboard/model/board/types';
import { createVMLDOM } from 'components/vertical-move-list/dom';
import { defaultTranslations } from 'chessboard/model/move-list/translations';
import { Game } from 'chessboard/model/game/types';
import type { MoveList } from 'chessboard/model/move-list/types';
import { paths } from 'chessboard/config/paths';
import type { VML } from 'components/vertical-move-list/config/types';
import 'components/vertical-move-list/vml.scss';
import { getTimestampElementParameters } from 'components/vertical-move-list/model/format-time';
import { getTimestampFromNode } from 'chessboard/model/move/get-timestamp-from-node';
import type JCE from '@chesscom/js-chess-engine';

/**
 * Create a vertical move list on a given element.
 */
export function createVML({
  colorsEnabled = false,
  el,
  epIconInfoPath = paths.enPassant, // ! references to hardcoded paths need to be removed
  getTimeControl: initialGetTimeControl,
  formatTimestampAsHumanReadable = false,
  moveListDisplayType = Board.MoveListDisplayTypes.Text,
  scrollContainer,
  showResultNode = true,
  showTimestamps = true,
  toggleTimestamps = false,
  translations = defaultTranslations,
}: {
  colorsEnabled?: boolean;
  el: HTMLElement;
  epIconInfoPath?: string;
  getTimeControl?: VML.GetTimeControl;
  formatTimestampAsHumanReadable?: boolean;
  id?: string;
  moveListDisplayType?: Board.MoveListDisplayTypes;
  scrollContainer: HTMLElement;
  showResultNode?: boolean;
  showTimestamps?: boolean;
  toggleTimestamps?: boolean;
  translations?: MoveList.Translations;
}): VML.API {
  let getTimeControl = initialGetTimeControl;

  const dom = createVMLDOM({
    colorsEnabled,
    el,
    EPIconInfoPath: epIconInfoPath,
    moveListDisplayType,
    scrollContainer,
    toggleTimestamps,
    translations,
  });

  dom.createList();

  return {
    addNode,
    addThreeFoldIcons,
    create: dom.createList,
    deselectNode: dom.deselectNode,
    destroy: dom.destroyList,
    enableColors: dom.enableColors,
    loadNodes,
    selectNode,
    setMoveListDisplayType,
    setResult,
    setGetTimeControl,
    updateNodeColor: dom.updateNodeColor,
    updateTimestamp,
  };

  /**
   * Add node.
   */
  function addNode(node: Game.Move.Node): void {
    /**
     * In the unlikely (but possible) event that the result node is already present, adding
     * a node becomes much more difficult and messy. It's easier to simply remove it and
     * reinstate after the move node has been added.
     */
    const result = dom.getResult();

    if (result !== Game.Results.None) {
      dom.setResultNode(Game.Results.None);
    }

    dom.addNode(node);

    addTimestamp(node);

    if (result !== Game.Results.None) {
      dom.setResultNode(result);
    }
  }

  /**
   * Add a node's timestamp.
   *
   * We define two possible types of time markers for a node.
   *
   * 1. `time` - this would be better named "gameTimeLeft". It's the time left on the clock when
   *    a move is made. The `time` term is baked into JCE so would be very difficult to change.
   *    This would typically be added by a "live" game where the time constraint of the game is
   *    "total game time" (e.g. blitz, bullet, classical, etc.).
   * 2. `timestamp` - this would be better named "moveTimeTaken", but historically we've referred
   *    to them as `timestamp`s. THis would typically be added by a "correspondence" game where
   *    the time constraint is "total move time" (e.g. daily).
   *
   * Both of the above values are added to the node object itself.
   *
   * When adding a timestamp to the move list, we need to either use the `timestamp` value
   * provided (that takes precedence) or convert the "gameTimeLeft" (`time`) to a "moveTimeTaken".
   *
   * Once we have that value, we need to convert it either to a "clock" string (e.g. 01:20:23) or
   * a "human readable" string (e.g. 3 mins).
   *
   * All of the above is done in this function.
   *
   * NB: Both `time` and `timestamp` are in deciseconds.
   */
  function addTimestamp(node: Game.Move.Node): void {
    const timestamp = getTimestampFromNode(node);

    if (!timestamp || !showTimestamps) {
      return;
    }

    const { title, value } = getTimestampElementParameters({
      formatTimestampAsHumanReadable,
      timestamp,
      translations,
    });

    dom.addTimestamp({
      baseTime: getBaseTime(),
      node,
      timestamp,
      title,
      value,
    });
  }

  /**
   * Get the current base time.
   */
  function getBaseTime(): number | undefined {
    return getTimeControl?.()?.baseTime;
  }

  /**
   * Load nodes.
   */
  function loadNodes(nodes: Game.Move.Node[], selectedPly?: number): void {
    /**
     * Cache the result here so that it can be reinstated later. It's an idiosyncrasy
     * of the move list DOM structure that it is far simpler to destroy and rebuild
     * the move list than it is to remove all the nodes and load new ones whilst
     * keeping the result node intact.
     */
    const result = dom.getResult();

    dom.clearList();

    nodes.forEach((node) => {
      dom.addNode(node);
      addTimestamp(node);
    });

    /**
     * Now reinstate the result node.
     */
    if (result !== Game.Results.None) {
      dom.setResultNode(result);
    }

    const ply = selectedPly ?? dom.getLastPly();

    if (ply !== undefined) {
      dom.selectNode(ply);
    }

    dom.scrollToSelectedNode();
  }

  /**
   * Select a node.
   */
  function selectNode(node: HTMLElement | number | undefined): void {
    if (node === undefined) {
      dom.deselectNode();
    } else if (typeof node === 'number') {
      dom.selectNode(node);
    } else {
      dom.selectNodeElement(node);
    }

    dom.scrollToSelectedNode();
  }

  /**
   * Set the moveListDisplayType.
   */
  function setMoveListDisplayType(type: Board.MoveListDisplayTypes): void {
    dom.setParams({ moveListDisplayType: type });
  }

  /**
   * Set the result node.
   */
  function setResult(result: Game.Results): void {
    if (!showResultNode) {
      if (dom.getResult() !== Game.Results.None) {
        dom.setResultNode(Game.Results.None);
      }

      return;
    }

    dom.setResultNode(result);
  }

  /**
   * Add draw icons to draw moves.
   */
  function addThreeFoldIcons(line: JCE.Line): void {
    dom.addThreeFoldMoveIcons(line);
  }

  /**
   * Externally set the getTimeControl function.
   */
  function setGetTimeControl(getTimeControlFn: VML.GetTimeControl) {
    getTimeControl = getTimeControlFn;
  }

  /**
   * Update a timestamp in the move list.
   */
  function updateTimestamp(node: Game.Move.Node): void {
    const { ply } = node;
    const timestamp = getTimestampFromNode(node);

    if (!ply) {
      return;
    }

    if (timestamp === null) {
      return dom.removeTimestamp(ply);
    }

    const { title, value } = getTimestampElementParameters({
      formatTimestampAsHumanReadable,
      timestamp,
      translations,
    });

    const existingTime = dom.getTimestamp(ply);

    if (!existingTime) {
      dom.addTimestamp({
        baseTime: getBaseTime(),
        node,
        timestamp,
        title,
        value,
      });
    } else if (existingTime !== timestamp) {
      dom.setTimestampValue({
        baseTime: getBaseTime(),
        ply,
        timestamp,
        value,
      });
    }
  }
}
