    ///
    /// Droppable pieces in Crazyhouse/Bughouse.
    /// The pieces in the hand are represented by a single number.
    /// The first 5 bits encode for pawns (max 31, 16 in real game)
    /// The next 3 encode for knights (max 7, 4 in real game)
    /// The next 3 encode for bishops (max 7, 4 in real game)
    /// The next 3 encode for rooks (max 7, 4 in real game)
    /// The final 2 encode for queens (max 64+, 2 in real game)
    /// Thus the shifts are an accumulations of the above.
    /// Examples:
    ///               binary  decimal   result
    ///                    0 or     0 = no pieces
    ///                    1 or     1 = 1 pawn
    ///                   10 or     2 = 2 pawns
    ///                   11 or     3 = 3 pawns
    ///               100000 or    32 = 1 knight
    ///               100001 or    33 = 1 knight and 1 pawn
    ///            100100001 or   289 = 1 bishop, 1 knight, and 1 pawn
    ///         100100100001 or  2337 = 1 rook, 1 bishop, 1 knight, and 1 pawn
    ///         100100100010 or  2338 = 1 rook, 1 bishop, 1 knight, and 2 pawns
    ///         100100101000 or  2344 = 1 rook, 1 bishop, 1 knight, and 8 pawns
    ///      100100100100001 or 18721 = 1 queen, 1 rook, 1 bishop, 1 knight, and 1 pawn
    ///     1010010010010000 or 42128 = 2 queens, 4 rooks, 4 bishops, 4 knights, and 16 pawns (maximum possible in Crazyhouse or Bughouse)
