                    /// If there was no rook, it will be 0; if there is more than one rook on a side, there will be multiple positive bits in the number.
                    /// (flagFile & flagFile - 1) checks to see if there is only one bit in the binary number (i.e., a Hamming weight of 1).
                    if (!flagFile || (flagFile & flagFile - 1) !== 0) {
                        return 15;
                    }
