        function beautifyMove(moveNum, knownSan, skipSymbol)
        {
            var from;
            var additionalInfo;
            var to = moveNum >> TO_SHIFT & SQUARE_MASK;
            var move = {
                color: TURN,
                to: algebraic(to),
                flags: moveNum & SQUARE_MASK,
            };
            
            if (!IS_DROP || (moveNum & BITS.DROP) === 0) {
                from = moveNum >> FROM_SHIFT & SQUARE_MASK;
                move.from = algebraic(from);
                move.piece = BOARD[from];
                if (moveNum & BITS.PROMOTION) {
                    additionalInfo = moveNum >> ADDITIONAL_SHIFT;
                    move.promotion = HAND_PIECES[additionalInfo] + TURN;
                }
                
                if (moveNum & BITS.CAPTURE) {
                    move.captured = BOARD[to];
                } else if (moveNum & BITS.EP_CAPTURE) {
                    move.captured = PAWN | swapColor(TURN);
                }
            } else {
                additionalInfo = moveNum >> ADDITIONAL_SHIFT;
                move.drop = additionalInfo;
                move.piece = HAND_PIECES[additionalInfo] + TURN;
            }
            
            if (knownSan) {
                move.san = knownSan;
            } else {
                move.san = moveToSan(moveNum, skipSymbol);
            }
            
            return move;
        }
        
