    /**
     * Utility function for retreiving information about a position (game over, in check, draw, etc)
     * @method getPositionInfo
     * @param gameSetup {string || object} a FEN string for the position or a game object (see createGame)
     * @param moves {array} moves (optional)
     * @param options {mixed} (optional) if boolean, it is interpreted as Chess960.
     * @return positionInfo
     */
    function getPositionInfo(gameSetup, moves, options)
    {
        var variant;
        var res = {
            gameOver:     false,
            check:        false,
            checkmate:    false,
            draw:         false,
            stalemate:    false,
            threefold:    false,
            insufficient: false,
        };
        var chess;
        
        /// Make moves optional.
        if (!Array.isArray(moves)) {
            options = moves;
            moves = null;
        }
        
        if (typeof options === "boolean") {
            variant = options;
            options = {};
        } else {
            options = options || {};
            variant = options.variant;
        }
        
        /// gameSetup can be a FEN string or an object describing a game.
        if (typeof gameSetup === "string") {
            chess = chessJS;
            chess.reset();
            if (chess.validateFen(gameSetup, variant) !== 0 || !chess.load(gameSetup, variant)) {
                return false;
            }
        } else {
            chess = createGame(gameSetup);
        }
        
        if (moves && !makeMoves(chess, moves, options)) {
            return false;
        }
        
        if (options.getLegalMoves) {
            res.legalMoves = getLegalMoves({game: chess});
        }
        
        if (chess.inCheck()) {
            res.check = true;
            res.checkmate = chess.inCheckmate();
        } else {
            res.stalemate = chess.inStalemate();
        }
        
        res.threefold     = chess.inThreefoldRepetition();
        res.insufficient  = chess.insufficientMaterial(options.color);
        res.fiftyMoveRule = chess.in50MoveRule();
        res.draw          = res.stalemate || res.threefold || res.insufficient || res.fiftyMoveRule || chess.inDraw();
        
        if (!options.skipValidation) {
            res.isValid = chess.isPositionValid(options);
        }
        
        if (variant) {
            res.variantWin = chess.isVariantWin();
            if (res.variantWin === undefined) {
                res.variantWin = chess.isBughouseWin();
            }
        }
        
        res.gameOver = res.checkmate || res.draw || Boolean(res.variantWin);
        
        return res;
    }
    
