export { createHorizontalMoveListPlugin } from 'chessboard/plugins/horizontal-move-list';
export { createBoardImagesPlugin } from 'chessboard/plugins/board-images';

/**
 * The minimal set of plugins to create a working chessboard.
 */
export function createMinimalPlugins() {
  return [
    createAssetsPlugin(),
    createBoardSizePlugin(),
    createPiecesPlugin(),
    createRendererErrorCatcherPlugin(),
  ];
}

/**
 * The "standard" package includes all the basic functionality that is required on
 * chess.com (blinking squares, coordinates, hover square, move hints, sounds, etc.).
 */
export function createStandardPlugins({ exclude }: { exclude?: string[] } = {}) {
  const plugins = {
    [Keys.Plugin.Name]: createKeysPlugin,
    [Accessibility.Plugin.Name]: createAccessibilityPlugin,
    [ArrowKeys.Plugin.Name]: createArrowKeysPlugin,
    [Arrows.Plugin.Name]: createArrowsPlugin,
    [BlinkingHighlight.Plugin.Name]: createBlinkingHighlightPlugin,
    [Coordinates.Plugin.Name]: createCoordinatesPlugin,
    [FlipBoard.Plugin.Name]: createFlipBoardPlugin,
    [Highlights.Plugin.Name]: createHighlightsPlugin,
    [HoverSquare.Plugin.Name]: createHoverSquarePlugin,
    [Markings.Plugin.Name]: createMarkingsPlugin,
    [BoardDisabled.Plugin.Name]: createBoardDisabledPlugin,
    [MoveHints.Plugin.Name]: createMoveHintsPlugin,
    [Sounds.Plugin.Name]: createSoundsPlugin,
  };

  if (exclude) {
    exclude.forEach((plugin) => {
      delete plugins[plugin];
    });
  }

  const highlightParams = [getSelectionSquare, getMoveHighlights, getAnalysisHighlights];
  const pluginArray: Plugins.Model[] = [...createMinimalPlugins()];

  Object.keys(plugins).forEach((pluginName) => {
    if (pluginName === Highlights.Plugin.Name) {
      return pluginArray.push(plugins[pluginName](highlightParams));
    }

    return pluginArray.push(plugins[pluginName]());
  });

  return pluginArray;
}

/**
 * The "premium" package is a superset of the standard package including extra features that are common but not used on every page
 */
export function createPremiumPlugins() {
  return [
    ...createStandardPlugins(),
    createCheatDetectionPlugin(),
    createCustomItemsPlugin(),
    createEffectsPlugin(),
    createFadeSetupPlugin(),
  ];
}

/**
 * The standard package plus all the extras like effects, custom items, etc.
 */
export function createKitchenSinkPlugins() {
  return [
    ...createPremiumPlugins(),
    createAnalysisOverlayPlugin(),
    createDebugPlugin(),
    createRecorderPlugin(),
    createVideoSyncPlugin(),
  ];
}
