export namespace LegacyThemes {
  export interface BoardStyles {
    [key: string]: string[];
  }

  export interface PieceStyles {
    [key: string]: { format: string; path: string; isPseudo3d?: boolean };
  }

  export interface Model {
    darkSquareColor: string;
    lightSquareColor: string;
    path: string;
    extension: string;
    highlightColor: string;
  }
}

