/**
 * Get the theme properties as an object.
 * Based on the legacy hardcoded themes.
 */
export function getLegacyThemeValues(boardStyle: string): LegacyThemes.Model | null {
  const themeArr = defaultLegacyBoardStyles[boardStyle];

  if (!themeArr) {
    return null;
  }

  const [darkSquareColor, lightSquareColor, path, extension, highlightColor] = themeArr;

  return {
    darkSquareColor,
    lightSquareColor,
    path,
    extension,
    highlightColor,
  };
}
