import type { Modes } from 'chessboard/modes/config/types';

export namespace ObservingMode {
  export interface API {
    clearCachedIds(): void;
    getCachedIds(): ObservingMode.CachedIds | undefined;
    isAnalyzing(): boolean;
    restorePosition(): void;
    setCachedIds(move: number, line: number): void;
    setCachedPGN(pgn: string): void;
    getCachedPGN(): string;
  }

  export enum APIEvents {
    AddMove = 'AddMove',
    AnalysisEnd = 'AnalysisEnd',
    AnalysisStart = 'AnalysisStart',
  }

  export interface CachedIds {
    move: number;
    line: number;
  }

  export namespace Plugin {
    export const Name = 'observing';
  }

  export interface PublicAPI {
    [Modes.Types.Observing]: ObservingMode.API;
  }
}
