import type { VML as VMLTypes } from 'components/vertical-move-list/config/types';

export namespace VML {
  export interface API {
    activate: () => void;
    deactivate: () => void;
    getMoveList: () => VMLTypes.API;
    isActive: () => boolean;
    isReady: () => boolean;
    waitForVMLReady: () => Promise<VMLTypes.API>;
  }

  export enum Events {
    Ready = 'Ready',
    Updated = 'Updated',
  }

  export interface Options {
    displayTimeInClockFormat?: boolean;
    formatTimestampAsHumanReadable?: boolean;
    loadLineOnCreate?: boolean;
    timestampsWithBaseTime?: boolean;
  }

  export namespace Plugin {
    export const Name = 'vml';

    export interface PublicAPI {
      [Plugin.Name]: API;
    }
  }
}
