import type { API } from 'api/config/types';
import { Core } from 'core/config/types';
import type { Game } from 'model/game/types';
import type { Queue } from 'utils/queue/config/types';
import type { VML } from 'components/vertical-move-list/config/types';

/**
 * Create the updateMoveList function.
 */
export function createUpdateMoveList(moveList: VML.API) {
  /**
   * Update the move list.
   */
  return function updateMoveList({
    context,
    event,
    line,
    selectedNode,
    queue,
  }: {
    context: API.Context;
    event: Core.Event;
    line: Game.Line;
    selectedNode?: Game.Move.Node;
    queue: Queue.API;
  }) {
    const { data, type } = event;

    const selectedPly = selectedNode?.ply;

    if (type === Core.APIEvents.AddMove && data.listMove) {
      moveList.addNode(data.listMove);
    } else if (data && data.plyDiff === 1 && selectedNode) {
      moveList.addNode(selectedNode);
      moveList.selectNode(selectedPly);
    } else if (
      type === Core.APIEvents.CreateGame ||
      type === Core.APIEvents.Load ||
      type === Core.APIEvents.DeletePosition
    ) {
      moveList.loadNodes(line);
    } else if (type === Core.APIEvents.LineUpdated) {
      moveList.loadNodes(line);
      moveList.selectNode(selectedPly);
    } else if (data.moveListDisplayType) {
      moveList.setMoveListDisplayType(data.moveListDisplayType);
      moveList.loadNodes(line, selectedPly);
    } else if (
      type === Core.APIEvents.NodeUpdated &&
      data.changes &&
      Object.prototype.hasOwnProperty.call(data.changes, 'timestamp')
    ) {
      moveList.updateTimestamp(data.changes.node);
    } else if (type === Core.APIEvents.UpdatePGNHeaders && data.Result) {
      moveList.setResult(data.Result);
      if (context.game.inThreefoldRepetition()) {
        queue.add(moveList.addThreeFoldIcons, line);
      }
    } else if (selectedPly === 0) {
      moveList.deselectNode();
    } else if (type !== Core.OptionsEvents.Update) {
      moveList.selectNode(selectedPly);
    }

    if (data.changes && Object.prototype.hasOwnProperty.call(data.changes, 'customColor')) {
      moveList.updateNodeColor(data.ids.move + 1, data.changes.customColor);
    }

    return selectedPly;
  };
}
