import type { Game } from 'model/game/types';
import type { MoveList } from 'chessboard/model/move-list/types';
import type { Board } from 'model/board/types';
import type { TimeControl } from 'api/time-control/config/types';
import type JCE from '@chesscom/js-chess-engine';

export namespace VML {
  export interface API {
    addNode(node: Game.Move.Node, timestamp?: number): void;
    addThreeFoldIcons(line: JCE.Line | undefined): void;
    create(): void;
    deselectNode(ply?: number): void;
    destroy(): void;
    enableColors(enabled: boolean): void;
    loadNodes(nodes: Game.Move.Node[], selectedPly?: number): void;
    selectNode(ply: HTMLElement | number | undefined): void;
    setMoveListDisplayType(type: Board.MoveListDisplayTypes): void;
    setResult(result: Game.Results): void;
    setGetTimeControl(getTimeControl: GetTimeControl): void;
    updateNodeColor(ply: number, color?: string): void;
    updateTimestamp(node: Game.Move.Node): void;
  }

  export namespace DOM {
    export interface API {
      addMove(moveNumber: number): void;
      addNode(node: Game.Move.Node): void;
      addTimestamp({
        baseTime,
        node,
        timestamp,
        title,
        value,
      }: {
        baseTime?: number;
        node: Game.Move.Node;
        timestamp: number;
        title?: string;
        value?: string;
      }): void;
      clearList(): void;
      createList(): void;
      deselectNode(ply?: number): void;
      destroyList(): void;
      enableColors(enabled: boolean): void;
      getLastPly(): number | undefined;
      getParams(): Params;
      getResult(): Game.Results;
      getResultNodeElement(): HTMLElement | null;
      getTimestamp(ply: number): number | null;
      hasResultNode(): boolean;
      addThreeFoldMoveIcons(line: JCE.Line): void;
      removeTimestamp(ply: number): void;
      scrollToSelectedNode(): void;
      selectNode(ply: number): void;
      selectNodeElement(element: HTMLElement): void;
      setParams(params: Partial<Params>): void;
      setResultNode(result: Game.Results): void;
      setTimestampValue(
        {
          baseTime,
          ply,
          value,
          timestamp,
        }: {
          baseTime?: number;
          ply: number;
          timestamp: number;
          value: string;
        },
        timestampEl?: HTMLElement,
      ): void;
      updateNodeColor(ply: number, color?: string): void;
    }

    export const enum Attributes {
      Ply = 'data-ply',
      Time = 'data-time',
      WholeMoveNumber = 'data-whole-move-number',
      VMLElement = 'data-vml-element',
    }

    export const enum CSSVariables {
      TimeBarWidth = '--timeBarWidth',
      TimeValue = '--timeValue',
      MaxMoveTimeTaken = '--timeMaxValue',
      TimestampWidth = '--timestampWidth',
    }

    export const enum ClassNames {
      ColorsEnabled = 'movelist-colored',
      List = 'vertical-move-list',
      GameResult = 'game-result',
      Move = 'move',
      Node = 'node',
      Selected = 'selected',
      WithBaseTime = 'timestamps-with-base-time',
      ToggleTimestamps = 'toggle-timestamps',
    }

    export const enum ElementTypes {
      Timestamp = 'timestamp',
    }

    export type EventListeners = {
      el?: HTMLElement;
      eventName: string;
      handler: EventListener;
    }[];

    export interface Params {
      baseTime?: number;
      colorsEnabled: boolean;
      el: HTMLElement;
      EPIconInfoPath: string;
      moveListDisplayType: Board.MoveListDisplayTypes;
      scrollContainer: HTMLElement;
      toggleTimestamps: boolean;
      translations: MoveList.Translations;
    }
  }

  export enum Events {
    SelectNode = 'SelectNode',
  }

  export type GetTimeControl = () => TimeControl.Model | null;

  export interface Time {
    days: number;
    hours: number;
    minutes: number;
  }
}
