import type { API } from 'chessboard/api/config/types';
import { Board } from 'chessboard/model/board/types';
import { Core } from 'chessboard/core/config/types';
import { createCoreEvent } from 'chessboard/core/model/create-core-event';
import { EventEmitterUtil } from '@chesscom/web-ui';
import { createQueue } from 'utils/queue';
import { createReadyStateManager } from 'utils/create-ready-state-manager';
import { createUpdateMoveList } from 'plugins/vertical-move-list/update-move-list';
import { createVML } from 'components/vertical-move-list';
import { isOneOf } from 'utils/app/model/is-one-of';
import { defaultTranslations } from 'chessboard/model/move-list/translations';
import type { MoveList } from 'chessboard/model/move-list/types';
import { ObservingMode } from 'chessboard/modes/observing/config/types';
import type { Plugins } from 'chessboard/core/plugins/config/types';
import { positionChangeEvents } from 'chessboard/config/position-change-events';
import { VML } from 'components/vertical-move-list/config/types';
import { VML as VMLPlugin } from 'chessboard/plugins/vertical-move-list/config/types';
import { isNode } from 'chessboard/model/game/is-node';
import { is } from 'src/utils/app/model/is';

const { createEventEmitter } = EventEmitterUtil;

/**
 * Create a plugin that adds the vertical move list to a given element.
 */
export function createVerticalMoveListPlugin({
  colorsEnabled = false,
  displayTimeInClockFormat = true,
  el: vmlEl,
  emitter = createEventEmitter(),
  formatTimestampAsHumanReadable = !displayTimeInClockFormat,
  loadLineOnCreate = true,
  namespace = VMLPlugin.Plugin.Name,
  scrollContainer,
  showResultNode = true,
  showTimestamps = true,
  toggleTimestamps = false,
  translations = defaultTranslations,
}: {
  colorsEnabled?: boolean;
  el: HTMLElement;
  emitter?: EventEmitterUtil.EventEmitter;
  namespace?: string;
  scrollContainer?: HTMLElement;
  showResultNode?: boolean;
  showTimestamps?: boolean;
  toggleTimestamps?: boolean;
  translations?: MoveList.Translations;
} & VMLPlugin.Options): Plugins.Model {
  let isActive = true;
  let destroyVMLEventListeners: Function | undefined;

  const VMLReadyStateManager = createReadyStateManager<VML.API>();

  const queue = createQueue(false);

  const moveList = createVML({
    el: vmlEl,
    formatTimestampAsHumanReadable,
    getTimeControl: () => null,
    scrollContainer: scrollContainer || vmlEl,
    showResultNode,
    showTimestamps,
    toggleTimestamps,
    translations,
  });

  const updateMoveList = createUpdateMoveList(moveList);

  return {
    api: (context: API.Context) => ({
      activate: () => {
        setActiveStatus(true);
        updateVerticalMoveList(createCoreEvent(Core.APIEvents.Load), context.api);
      },
      deactivate: () => setActiveStatus(false),
      ...emitter,
      getMoveList: () => moveList,
      isActive: () => Boolean(isActive),
      isReady: () => VMLReadyStateManager.isReady(),
      waitForVMLReady: VMLReadyStateManager.waitForReady,
    }),
    create,
    destroy,
    match: [
      {
        condition: shouldUpdateMoveList,
        handler: (event: Core.Event, context: Core.Context) =>
          updateVerticalMoveList(event, context.api),
      },
      {
        condition: is(Core.OptionsEvents.Update),
        handler: (event: Core.Event) => {
          if ('enabled' in event.data) {
            setActiveStatus(event.data.enabled);
          }
        },
      },
    ],
    name: namespace,
  };

  /**
   * Create the VML component.
   */
  function create(context: Core.Context) {
    const { api } = context;

    moveList.setGetTimeControl(api.timeControl.get);

    moveList.create();

    destroyVMLEventListeners = createVMLEventListeners(api);

    if (colorsEnabled) {
      moveList.enableColors(colorsEnabled);
    }

    if (loadLineOnCreate) {
      const line = api.getLine(0);
      const selectedPly = api.getSelectedNode()?.ply;
      const displayType = api.getOptions().moveListDisplayType;

      if (displayType === Board.MoveListDisplayTypes.Figurine) {
        moveList.setMoveListDisplayType(displayType);
      }

      if (line?.length) {
        moveList.loadNodes(line, selectedPly);
      }

      if (showResultNode) {
        moveList.setResult(api.getResult());

        if (api.getContext().game.inThreefoldRepetition()) {
          moveList.addThreeFoldIcons(line);
        }
      }
    }

    VMLReadyStateManager.ready(moveList);

    emitter.emit(VMLPlugin.Events.Ready, moveList);

    isActive = api.getOptions().enabled;
  }

  /**
   * Destroy the move list.
   */
  function destroy() {
    destroyVMLEventListeners?.();
    queue.clear();

    if (!moveList) {
      return;
    }

    moveList.destroy();
  }

  /**
   * Create the listener for the VML's SelectNode event. Returns a function
   * that destroys the listener.
   */
  function createVMLEventListeners(api: API.ExtendedAPI) {
    vmlEl.addEventListener(VML.Events.SelectNode, selectNode as EventListener);

    /**
     * Returned function destroys the listener.
     */
    return function destroyListener() {
      vmlEl.removeEventListener(VML.Events.SelectNode, selectNode as EventListener);
    };

    /**
     * Select the node via the API.
     */
    function selectNode({ detail }: CustomEvent): void | API.Response {
      if (!isActive || api.getOptions().disableUserNavigation) {
        return;
      }
      const { ids } = detail;

      api.selectNode(ids.line, ids.move);
    }
  }

  /**
   * Update the vertical move list asynchronously.
   */
  function updateVerticalMoveList(event: Core.Event, api: API.ExtendedAPI) {
    const selectedNode = api.getRelativeNode();

    queue.add(updateMoveList, {
      context: api.getContext(),
      event,
      line: api.getLine?.(0),
      selectedNode: isNode(selectedNode) ? selectedNode : undefined,
      queue,
    });

    requestAnimationFrame(() => {
      requestAnimationFrame(function flushMoveListQueue() {
        queue.flush();

        emitter.emit(VMLPlugin.Events.Updated, moveList);
      });
    });
  }

  /**
   * Set the active status of the plugin.
   */
  function setActiveStatus(active: boolean) {
    isActive = active;
  }

  /**
   * Determine whether or not the move list should update.
   */
  function shouldUpdateMoveList(event: Core.Event) {
    return (
      isActive &&
      isOneOf([
        ...positionChangeEvents,
        ObservingMode.APIEvents.AddMove,
        Core.APIEvents.LineUpdated,
        Core.APIEvents.NodeUpdated,
        Core.APIEvents.DeletePosition,
        Core.APIEvents.UpdatePGNHeaders,
        Core.InstanceEvents.Create,
        Core.InstanceEvents.ModeChanged,
        Core.OptionsEvents.Update,
      ])(event)
    );
  }
}
