import type { VML } from 'src/components/vertical-move-list/config/types';
import { MoveList } from 'chessboard/model/move-list/types';

/**
 * Transform number of deciseconds into a days/hours object.
 *
 * @example
 * decisecondsToDaysHoursMinutesObject(25410600);
 * // { days: 2, hours: 22, minutes: 35 }
 */
export function decisecondsToDaysHoursMinutesObject(deciseconds: number): VML.Time {
  const roundedSeconds = Math.ceil(deciseconds / 10);
  let days = Math.floor(roundedSeconds / (3600 * 24));
  let hours = Math.floor((roundedSeconds % (3600 * 24)) / 3600);
  let minutes = Math.ceil((roundedSeconds % 3600) / 60);

  if (minutes === 60) {
    minutes = 0;
    hours += 1;
  }

  if (hours === 24) {
    hours = 0;
    days += 1;
  }

  return {
    days,
    hours,
    minutes,
  };
}

/**
 * Transform number of deciseconds into a days and hours string.
 *
 * @example
 * decisecondsToDaysHoursMinutesString(25410600);
 * // '2 days, 22 hours, 35 minutes'
 */
export function decisecondsToDaysHoursMinutesString(deciseconds: number, translations): string {
  const { days, hours, minutes } = decisecondsToDaysHoursMinutesObject(deciseconds);

  const padding = {
    days: hours > 0 || minutes > 0 ? ', ' : '',
    hours: minutes > 0 ? ', ' : '',
  };

  const daysString =
    days > 0
      ? `${translations[MoveList.Translation.Plural.NumberOfDays](days)}${padding.days}`
      : '';

  const hoursString =
    hours > 0
      ? `${translations[MoveList.Translation.Plural.NumberOfHours](hours)}${padding.hours}`
      : '';

  const minutesString =
    minutes > 0 ? `${translations[MoveList.Translation.Plural.NumberOfMinutes](minutes)}` : '';

  return `${daysString}${hoursString}${minutesString}`;
}

/**
 * Convert deciseconds to a human readable format.
 *
 * @example
 * decisecondsToHumanReadableFormat(25410600); // 2 days
 * decisecondsToHumanReadableFormat(110000); // 3 hours
 * decisecondsToHumanReadableFormat(25000); // 4 mins
 */
export function decisecondsToHumanReadableFormat(
  deciseconds: number,
  translations: MoveList.Translations,
): string {
  const { days, hours, minutes } = decisecondsToDaysHoursMinutesObject(deciseconds);

  if (days) {
    return days > 0 ? `${translations[MoveList.Translation.Plural.NumberOfDays](days)}` : '';
  }

  if (hours) {
    return hours > 0 ? `${translations[MoveList.Translation.Plural.NumberOfHours](hours)}` : '';
  }

  return minutes > 0 ? `${translations[MoveList.Translation.Plural.NumberOfMinutes](minutes)}` : '';
}

/**
 * Format deciseconds as clock string.
 */
export function formatDecisecondsAsClockString(deciseconds: number) {
  const minutes = Math.floor(deciseconds / 600);
  const seconds = ((deciseconds - minutes * 600) / 10).toFixed(1);

  if (minutes === 0) {
    return seconds;
  }

  const paddedSeconds = `0${seconds}`.substr(-4);

  return `${minutes}:${paddedSeconds}`;
}

/**
 * Get the parameters for a timestamp element.
 *
 * The `value` always returns a string here as
 * this is the actual value added to the innerHTML of the timestamp element.
 *
 * The `title` is only required
 */
export function getTimestampElementParameters({
  formatTimestampAsHumanReadable,
  timestamp,
  translations,
}: {
  formatTimestampAsHumanReadable: boolean;
  timestamp: number;
  translations: MoveList.Translations;
}) {
  return {
    title: formatTimestampAsHumanReadable
      ? decisecondsToDaysHoursMinutesString(timestamp, translations)
      : undefined,

    value: formatTimestampAsHumanReadable
      ? decisecondsToHumanReadableFormat(timestamp, translations)
      : formatDecisecondsAsClockString(timestamp),
  };
}
