import { MoveList } from 'chessboard/model/move-list/types';
import { MoveListShared } from 'components/move-list-shared/config/types';
import { Pointerdown } from 'chessboard/model/dom/get-pointer-events';

/**
 * Create an en passant icon and return it.
 */
export function createEnPassantIcon(linkPath: string, translations: MoveList.Translations) {
  const iconElWrapper = document.createElement('div');
  const iconEl = document.createElement(MoveListShared.Tags.EnPassantIcon);

  iconElWrapper.appendChild(iconEl);

  iconElWrapper.classList.add(MoveListShared.Classes.MoveInfoIcon);
  iconEl.classList.add(MoveListShared.Classes.IconFont, MoveListShared.Classes.CircleInfo);

  iconElWrapper.addEventListener(Pointerdown, EPIconPointerdownHandler);
  iconElWrapper.addEventListener('mouseover', positionTooltip);

  iconElWrapper.dataset.tooltip = translations[MoveList.Translation.Tooltips.EnPassant]();

  return {
    el: iconElWrapper,
    listener: {
      el: iconElWrapper,
      eventName: Pointerdown,
      handler: EPIconPointerdownHandler,
    },
  };

  /**
   * Position tooltip on hover
   */
  function positionTooltip() {
    const height = iconElWrapper.getBoundingClientRect().y;
    iconElWrapper.style.setProperty('--tooltip-top', `${height}px`);
  }

  /**
   * Handle a pointerdown event on the en passant icon.
   */
  function EPIconPointerdownHandler(e: Event): void {
    e.stopPropagation();

    window.open(linkPath, '_blank');
  }
}
