import type { Game } from 'chessboard/model/game/types';
import { getPieceName } from 'components/vertical-move-list/model/get-piece-name';

/**
 * Convert a move text character to a figurine HTML string if applicable.
 */
export function toFigurine(char: string, color: Game.ColorsAsWords) {
  return char.toUpperCase() === char ? getFigurineHTML(char, color) : char;
}

/**
 * Get the HTML string for a figurine.
 */
function getFigurineHTML(char: string, color: Game.ColorsAsWords): string {
  return `<span class="icon-font-chess ${getPieceName(
    char,
  )}-${color}" data-figurine="${char}"></span>`;
}
