import type JCE from '@chesscom/js-chess-engine';
import { findThreefoldRepetitions } from 'components/move-list/helpers/find-threefold-repetitions';

/**
 * Get move elements that resulted in draw
 */
export function getThreeFoldElements(
  line: JCE.Line,
  elements: HTMLElement[],
): HTMLElement[] | void {
  const indexes = findThreefoldRepetitions(line);

  if (!indexes) {
    return;
  }

  return indexes.map((ndx) => elements[ndx]);
}
