import { addCustomColor } from 'components/move-list-shared/model/add-custom-color';
import { createEnPassantIcon } from 'components/move-list-shared/model/create-en-passant-icon';
import { createState } from 'utils/state';
import { formatText } from 'model/move-list/format-text';
import { Game } from 'model/game/types';
import { getBarWidth } from 'components/vertical-move-list/model/get-bar-width';
import { getColorAsWord } from 'model/game/get-color-as-word';
import { getThreeFoldElements } from 'components/vertical-move-list/model/get-three-fold-elements';
import { getTimestampClass } from 'components/vertical-move-list/model/get-timestamp-class';
import { isEPCapture as isEnPassantCapture } from 'model/move/is-ep-capture';
import type JCE from '@chesscom/js-chess-engine';
import { millisecondsToDeciseconds } from 'utils/number/milliseconds-to-deciseconds';
import { Pointerdown } from 'model/dom/get-pointer-events';
import { TIMESTAMP_SINGLE_DIGIT_WIDTH } from 'components/vertical-move-list/config/constants';
import { toClassString } from 'utils/string/to-class-string';
import { VML } from 'components/vertical-move-list/config/types';
import { scrollToNode } from 'components/move-list-shared/scroll-to-node';
import { MoveList } from 'chessboard/model/move-list/types';
import { MoveListShared } from 'components/move-list-shared/config/types';

/**
 * Create the DOM manager for the VML.
 */
export function createVMLDOM(initialParams: VML.DOM.Params): VML.DOM.API {
  const { el, toggleTimestamps } = initialParams;

  /**
   * DOM listeners are cached here so that they can be destroyed along with the move list.
   */
  const listeners: VML.DOM.EventListeners = [];

  /**
   * Getters and setters for the params.
   */
  const params = createState(initialParams);

  return {
    addMove,
    addNode,
    addTimestamp,
    clearList,
    createList,
    deselectNode,
    destroyList,
    enableColors,
    getLastPly,
    getParams: params.get,
    getResult,
    getResultNodeElement,
    getTimestamp,
    hasResultNode,
    addThreeFoldMoveIcons,
    removeTimestamp,
    scrollToSelectedNode,
    selectNode,
    selectNodeElement,
    setResultNode,
    setTimestampValue,
    setParams: params.set,
    updateNodeColor,
  };

  /**
   * Add a move to the list.
   */
  function addMove(wholeMoveNumber?: number): HTMLElement {
    const moveEl = document.createElement('div');

    moveEl.classList.add(VML.DOM.ClassNames.Move);

    if (wholeMoveNumber) {
      moveEl.setAttribute(VML.DOM.Attributes.WholeMoveNumber, `${wholeMoveNumber}`);
      moveEl.innerHTML = `${wholeMoveNumber}.`;
    }

    el.appendChild(moveEl);

    return moveEl;
  }

  /**
   * Add a node to the list.
   */
  function addNode(node: Game.Move.Node): HTMLElement | undefined {
    const { color, flags, ids, ply, san, wholeMoveNumber } = node;

    if (
      !color ||
      !ply ||
      !san ||
      !wholeMoveNumber ||
      getNodeElement(ply) // node already exists
    ) {
      return;
    }

    let moveEl = getMoveElement(wholeMoveNumber);

    if (!moveEl) {
      moveEl = addMove(wholeMoveNumber);
    }

    const nodeEl = createNodeElement({
      color: getColorAsWord(color),
      moveId: ids.move,
      isEPCapture: flags ? isEnPassantCapture(flags) : false,
      ply,
      san,
    });

    if (!nodeEl) {
      return;
    }

    if (node.ply && Object.prototype.hasOwnProperty.call(node, 'customColor')) {
      addCustomColor(nodeEl, node.customColor);
    }

    return moveEl.appendChild(nodeEl);
  }

  /**
   * Add a timestamp to a node.
   */
  function addTimestamp({
    baseTime,
    node,
    timestamp,
    title,
    value,
  }: {
    baseTime?: number;
    node: Game.Move.Node;
    timestamp: number;
    title?: string;
    value: string;
  }): HTMLElement | undefined {
    const { color, ply, wholeMoveNumber } = node;

    if (!color || !ply || !wholeMoveNumber) {
      return;
    }

    const moveEl = getMoveElement(wholeMoveNumber);

    if (!moveEl) {
      return;
    }

    const existingTimestamp = getTimestampElement(ply);

    if (existingTimestamp) {
      moveEl.removeChild(existingTimestamp);
    }

    const timestampEl = createTimestampElement(ply);

    if (title) {
      setTimestampElementTitle(title, timestampEl);
    }

    setTimestampElementColor(color, timestampEl);
    setTimestampValue(
      {
        baseTime,
        ply,
        timestamp,
        value,
      },
      timestampEl,
    );

    moveEl.appendChild(timestampEl);

    return timestampEl;
  }

  /**
   * Clears the move list.
   */
  function clearList() {
    destroyList();
    createList();
  }

  /**
   * Create the move list.
   */
  function createList(): void {
    destroyList();

    el.classList.add(VML.DOM.ClassNames.List);

    if (toggleTimestamps) {
      el.classList.add(VML.DOM.ClassNames.ToggleTimestamps);
    }
  }

  /**
   * Create a node element.
   */
  function createNodeElement({
    color,
    isEPCapture,
    moveId,
    ply,
    san,
  }: {
    color: Game.ColorsAsWords;
    moveId?: number;
    isEPCapture?: boolean;
    ply: number;
    san: string;
  }): HTMLElement {
    const { EPIconInfoPath, moveListDisplayType, translations } = params.get();

    const nodeEl = document.createElement('div');

    nodeEl.setAttribute(VML.DOM.Attributes.Ply, ply.toString());
    nodeEl.classList.add(color, VML.DOM.ClassNames.Node);

    nodeEl.innerHTML = formatText({
      color,
      moveListDisplayType,
      san,
      translations,
    });

    if (isEPCapture) {
      const { el: EPIconElement, listener: EPIconListener } = createEnPassantIcon(
        EPIconInfoPath,
        translations,
      );

      nodeEl.appendChild(EPIconElement);

      listeners.push({
        el: EPIconElement,
        eventName: Pointerdown,
        handler: EPIconListener.handler,
      });
    }

    nodeEl.addEventListener(Pointerdown, halfMovePointerdownHandler);

    listeners.push({
      eventName: Pointerdown,
      handler: halfMovePointerdownHandler,
    });

    return nodeEl;

    /**
     * Handle the pointerdown event on the half move.
     */
    function halfMovePointerdownHandler() {
      el.dispatchEvent(
        new CustomEvent(VML.Events.SelectNode, { detail: { ids: { move: moveId, line: 0 }, ply } }),
      );
    }
  }

  /**
   * Create a result node element.
   */
  function createResultNodeElement(res: Game.Results, color: Game.ColorsAsWords): HTMLElement {
    const resultEl = document.createElement('span');

    resultEl.innerHTML = res;
    resultEl.classList.add(color, VML.DOM.ClassNames.GameResult);

    return resultEl;
  }

  /**
   * Create a 3-fold-move icon element.
   */
  function createThreeFoldIconElement(): HTMLElement {
    const { translations } = params.get();
    const iconElWrapper = document.createElement('div');
    const iconEl = document.createElement('span');

    iconElWrapper.appendChild(iconEl);

    iconElWrapper.classList.add(MoveListShared.Classes.MoveInfoIcon);
    iconEl.classList.add(MoveListShared.Classes.IconFont, MoveListShared.Classes.ThreeFoldIcon);

    iconElWrapper.dataset.tooltip =
      translations[MoveList.Translation.Tooltips.ThreeFoldRepetition]();

    iconElWrapper.addEventListener('mouseover', positionTooltip);

    return iconElWrapper;

    /**
     * Position tooltip on hover
     */
    function positionTooltip() {
      const height = iconElWrapper.getBoundingClientRect().y;
      iconElWrapper.style.setProperty('--tooltip-top', `${height}px`);
    }
  }

  /**
   * Create a timestamp element.
   */
  function createTimestampElement(ply: number): HTMLElement {
    const timestampEl = document.createElement('div');

    timestampEl.setAttribute(VML.DOM.Attributes.Ply, ply.toString());
    timestampEl.setAttribute(VML.DOM.Attributes.VMLElement, VML.DOM.ElementTypes.Timestamp);

    return timestampEl;
  }

  /**
   * Deselects a node by ply (deselects the selected node if no ply provided).
   */
  function deselectNode(ply?: number): void {
    const element = ply ? getNodeElement(ply) : getSelectedNodeElement();

    if (!element) {
      return;
    }

    deselectNodeElement(element);
  }

  /**
   * Deselect a node element.
   */
  function deselectNodeElement(element: HTMLElement): void {
    element.classList.remove(VML.DOM.ClassNames.Selected);
  }

  /**
   * Destroy the move list.
   */
  function destroyList(): void {
    listeners.forEach((listener) => {
      el.removeEventListener(listener.eventName, listener.handler);
    });

    el.classList.remove(VML.DOM.ClassNames.List);

    el.innerHTML = '';
  }

  /**
   * Enable colors on the move list.
   */
  function enableColors(enabled: boolean): void {
    const { colorsEnabled } = params.get();

    if (colorsEnabled === enabled) {
      return;
    }

    params.set({ colorsEnabled: enabled });

    el.classList[enabled ? 'add' : 'remove'](VML.DOM.ClassNames.ColorsEnabled);
  }

  /**
   * Get the ply of the last node in the list.
   */
  function getLastPly(): number | undefined {
    const nodeElements = getNodeElements();

    if (!nodeElements.length) {
      return;
    }

    const ply = getNodeElementPly(nodeElements[nodeElements.length - 1]);

    if (!ply) {
      return;
    }

    return ply;
  }

  /**
   * Get the maximum move time taken in milliseconds.
   */
  function getMaxMoveTimeTaken(): number | null {
    const timeString = el.style.getPropertyValue(VML.DOM.CSSVariables.MaxMoveTimeTaken);

    if (!timeString.length) {
      return null;
    }

    return parseFloat(timeString);
  }

  /**
   * Get a move element by whole move number.
   */
  function getMoveElement(wholeMoveNumber: number): HTMLElement | null {
    return el.querySelector(`[${VML.DOM.Attributes.WholeMoveNumber}="${wholeMoveNumber}"]`);
  }

  /**
   * Get an array of all move elements.
   */
  function getMoveElements(): HTMLElement[] {
    return Array.from(
      el.querySelectorAll(`${toClassString(VML.DOM.ClassNames.Move)}`),
    ) as HTMLElement[];
  }

  /**
   * Get the color of a node element.
   */
  function getNodeElementColor(element: HTMLElement): Game.ColorsAsNumbers {
    return element.classList.contains(Game.ColorsAsWords.Black)
      ? Game.ColorsAsNumbers.Black
      : Game.ColorsAsNumbers.White;
  }

  /**
   * Get the ply of a node element.
   */
  function getNodeElementPly(element: HTMLElement): number | null {
    const ply = element.getAttribute(VML.DOM.Attributes.Ply);

    return ply ? parseInt(ply, 10) : null;
  }

  /**
   * Get a node element by ply.
   */
  function getNodeElement(ply: number): HTMLElement | null {
    return el.querySelector(
      `${toClassString(VML.DOM.ClassNames.Node)}[${VML.DOM.Attributes.Ply}="${ply}"]`,
    );
  }

  /**
   * Get an array of all node elements.
   */
  function getNodeElements(): HTMLElement[] {
    return Array.from(
      el.querySelectorAll(`${toClassString(VML.DOM.ClassNames.Node)}`),
    ) as HTMLElement[];
  }

  /**
   * Get the result of the game.
   */
  function getResult(): Game.Results {
    const resultNode = getResultNodeElement();

    if (!resultNode) {
      return Game.Results.None;
    }

    return <Game.Results>resultNode.innerText;
  }

  /**
   * Get the result node element if it exists.
   */
  function getResultNodeElement(): HTMLElement | null {
    return el.querySelector(toClassString(VML.DOM.ClassNames.GameResult));
  }

  /**
   * Get the timestamp value by ply.
   */
  function getTimestamp(ply: number): number | null {
    const timestampEl = getTimestampElement(ply);

    if (!timestampEl) {
      return null;
    }

    const timeString = timestampEl.getAttribute(VML.DOM.Attributes.Time);

    if (timeString?.length === 0) {
      return null;
    }

    return parseFloat(<string>timeString);
  }

  /**
   * Get the timestamp element for a node by ply.
   */
  function getTimestampElement(ply: number): HTMLElement | null {
    return el.querySelector(
      `[${VML.DOM.Attributes.VMLElement}="${VML.DOM.ElementTypes.Timestamp}"][${VML.DOM.Attributes.Ply}="${ply}"]`,
    );
  }

  /**
   * Get the current width of the timestamps (they are all the same width).
   */
  function getTimestampWidth(): number | null {
    const timestampWidthStr = el.style.getPropertyValue(VML.DOM.CSSVariables.TimestampWidth);

    if (!timestampWidthStr.length) {
      return null;
    }

    return parseFloat(timestampWidthStr);
  }

  /**
   * Get the selected node element if it exists.
   */
  function getSelectedNodeElement(): HTMLElement | null {
    return el.querySelector(toClassString(VML.DOM.ClassNames.Selected));
  }

  /**
   * Determine if the result node exists on the VML
   */
  function hasResultNode(): boolean {
    return Boolean(el.querySelector(toClassString(VML.DOM.ClassNames.GameResult)));
  }

  /**
   * Remove a timestamp from a node by ply.
   */
  function removeTimestamp(ply: number): void {
    const timestampEl = getTimestampElement(ply);

    if (!timestampEl) {
      return;
    }

    (<HTMLElement>timestampEl.parentNode).removeChild(timestampEl);
  }

  /**
   * Scroll to the currently selected node.
   */
  function scrollToSelectedNode(): void {
    const { scrollContainer } = params.get();

    const selectedNodeEl = getSelectedNodeElement();
    const container = scrollContainer || el;

    if (!selectedNodeEl) {
      container.scrollTop = 0;

      return;
    }

    scrollToNode({
      containerEl: container,
      moveListEl: el,
      nodeEl: selectedNodeEl,
    });
  }

  /**
   * Select a node element.
   */
  function selectNodeElement(element: HTMLElement): void {
    const selectedNodeEl = getSelectedNodeElement();

    if (element === selectedNodeEl) {
      return;
    }

    if (selectedNodeEl) {
      deselectNodeElement(selectedNodeEl);
    }

    element.classList.add(VML.DOM.ClassNames.Selected);
  }

  /**
   * Select a node by ply.
   */
  function selectNode(ply: number) {
    const node = getNodeElement(ply);

    if (!node) {
      return;
    }

    selectNodeElement(node);
  }

  /**
   * Set the maximum move time taken.
   */
  function setMaxMoveTimeTaken(time: number | null): void {
    if (time === null) {
      el.style.removeProperty(VML.DOM.CSSVariables.MaxMoveTimeTaken);

      return;
    }

    el.style.setProperty(VML.DOM.CSSVariables.MaxMoveTimeTaken, (<number>time).toString());
  }

  /**
   * Set the result node value.
   */
  function setResultNode(newResult: Game.Results): void {
    const resultNode = getResultNodeElement();

    if (resultNode !== null) {
      const parent = resultNode.parentNode;

      if (!parent) {
        return;
      }

      (<HTMLElement>parent).removeChild(resultNode);

      /**
       * If the result node was the only node in the move element, remove the move
       * element too.
       */
      if (parent.childNodes.length === 0) {
        (<HTMLElement>parent.parentNode).removeChild(parent);
      }
    }

    if (newResult === Game.Results.None) {
      return;
    }

    const lastNodeEl = getNodeElements().slice(-1)[0];

    const color =
      !lastNodeEl || getNodeElementColor(lastNodeEl) === Game.ColorsAsNumbers.Black
        ? Game.ColorsAsWords.White
        : Game.ColorsAsWords.Black;

    const moveEl = color === Game.ColorsAsWords.White ? addMove() : getMoveElements().slice(-1)[0];

    const resultNodeEl = createResultNodeElement(newResult, color);

    moveEl.appendChild(resultNodeEl);
  }

  /**
   * Create 3-fold repetition move icons
   */
  function addThreeFoldMoveIcons(line: JCE.Line) {
    const threeFoldMoveEls = getThreeFoldElements(line, getNodeElements());

    if (!threeFoldMoveEls || threeFoldMoveEls.length !== 3) {
      return;
    }

    threeFoldMoveEls.forEach((nodeEl) => {
      if (!nodeEl.querySelector(toClassString(MoveListShared.Classes.MoveInfoIcon))) {
        nodeEl.appendChild(createThreeFoldIconElement());
      }
    });
  }

  /**
   * Set the color of a timestamp element.
   */
  function setTimestampElementColor(color: Game.ColorsAsNumbers, timestampEl: HTMLElement): void {
    timestampEl.classList.add(getTimestampClass(getColorAsWord(color)));
  }

  /**
   * Set the title attribute of a timestamp.
   */
  function setTimestampElementTitle(title: string, timestampEl: HTMLElement): void {
    timestampEl.title = title;
  }

  /**
   * Set the value of a timestamp by ply.
   *
   * This function also:
   * 1. Updates the width of all timestamps if the new timestamp is too long.
   * 2. Sets the bar width (if a base time is set) OR sets the max move time taken.
   */
  function setTimestampValue(
    {
      baseTime,
      ply,
      value,
      timestamp,
    }: {
      baseTime?: number;
      ply: number;
      timestamp: number;
      value: string;
    },
    element?: HTMLElement,
  ): void {
    const timestampEl = element || getTimestampElement(ply);

    if (!timestampEl) {
      return;
    }

    /**
     * DOM getters.
     */
    const timestampWidth = getTimestampWidth();

    const maxMoveTimeTaken = getMaxMoveTimeTaken();

    /**
     * DOM setters.
     */
    timestampEl.setAttribute(VML.DOM.Attributes.Time, timestamp.toString());
    timestampEl.style.setProperty(VML.DOM.CSSVariables.TimeValue, timestamp.toString());
    timestampEl.innerHTML = value;

    /**
     * If current timestamp's text string is the longest in the VML, we need to adjust the
     * width of all the timestamp elements, so that the text is left-aligned.
     *
     * e.g.:
     *
     * | ========= 1:00:00 |
     * |    ====== 01:00   |
     */
    if (!timestampWidth || value.length * TIMESTAMP_SINGLE_DIGIT_WIDTH > timestampWidth) {
      setTimestampWidth(value.length);
    }

    /**
     * There are two ways that the timestamp bar width is calculated, depending on if the
     * game is constrained by total game time, or total time per move.
     *
     * If a base time is set, the timestamp's bar width is proportional to the approximate
     * average move time (which assumes 40 moves per game).
     */
    if (baseTime) {
      const barWidth = getBarWidth(timestamp, millisecondsToDeciseconds(baseTime));

      timestampEl.style.setProperty(VML.DOM.CSSVariables.TimeBarWidth, `${barWidth}px`);

      el.classList.add(VML.DOM.ClassNames.WithBaseTime);

      if (maxMoveTimeTaken) {
        setMaxMoveTimeTaken(null);
      }

      return;
    }

    el.classList.remove(VML.DOM.ClassNames.WithBaseTime);

    /**
     * If there's no base time then the width of the bar is based on the time relative to the
     * longest time taken for any move in the game, with the longest taking up the full bar width.
     *
     * The longest move time taken is stored as a CSS variable, and used in the width calculation
     * for each timestamp.
     */
    if (!maxMoveTimeTaken || timestamp > maxMoveTimeTaken) {
      setMaxMoveTimeTaken(timestamp);
    }
  }

  /**
   * Set the width of the timestamps in pixels.
   */
  function setTimestampWidth(width: number): void {
    const newTimestampElWidth = width * TIMESTAMP_SINGLE_DIGIT_WIDTH;

    el.style.setProperty(VML.DOM.CSSVariables.TimestampWidth, `${newTimestampElWidth}px`);
  }

  /**
   * Update the color of a node.
   */
  function updateNodeColor(ply: number, color?: string) {
    const nodeEl = getNodeElement(ply);

    if (!nodeEl) {
      return;
    }

    addCustomColor(nodeEl, color);
  }
}
