import type { Game } from 'chessboard/model/game/types';
import { Board } from 'chessboard/model/board/types';
import { toFigurine } from 'chessboard/model/move-list/to-figurine';
import type { MoveList } from 'chessboard/model/move-list/types';

/**
 * Format a text node.
 */
export function formatText({
  color,
  moveListDisplayType,
  san,
  translations,
}: {
  color: Game.ColorsAsWords;
  moveListDisplayType: Board.MoveListDisplayTypes;
  san: string;
  translations: MoveList.Translations;
}) {
  return san
    .split('')
    .map((char) => {
      if (!'NBRQK'.includes(char)) {
        return char;
      }

      return moveListDisplayType === Board.MoveListDisplayTypes.Figurine
        ? toFigurine(char, color)
        : translations[char]();
    })
    .join('');
}
