/**
 * Create a state instance.
 */
export function createState<T = any>(initial: T = <any>{}) {
  const state: T = { ...initial };

  return {
    get,
    set,
  };

  /**
   * Get the state.
   */
  function get(): T {
    return state;
  }

  /**
   * Set the state.
   */
  function set(partialState: Partial<T>) {
    Object.assign(state, partialState);
  }
}
