import { createExternallyResolvablePromise } from 'utils/promise/create-externally-resolvable-promise';

export interface ReadyStateManager<T> {
  isReady(): boolean;
  ready(data: T): void;
  waitForReady: () => Promise<T>;
}

/**
 * Provides management of a "ready" state with isReady and waitForReady
 * methods.
 */
export function createReadyStateManager<T>(): ReadyStateManager<T> {
  let isReady = false;
  const resolvablePromise = createExternallyResolvablePromise();

  return {
    isReady: () => isReady,
    ready,
    waitForReady: () => <Promise<T>>resolvablePromise.promise,
  };

  /**
   * Set state as ready with some arbitrary data to return.
   */
  function ready(data: T) {
    isReady = true;
    resolvablePromise.resolve?.(data);
  }
}
